@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
body {
  margin: 0;
  padding: 0; 
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  height: 100vh; 
  width: 100vw; 
  text-align: center;
 
}

.App-modal {
  width: 50%; 
  margin: auto; 
  margin-top: 5rem; 
}


.ColoringPage-sketch {
    padding-left: 4rem;
    padding-bottom: 0.5rem;
     
    height: 100%; 
    width: 100%; 
    margin: 0; 
    padding: 0;   
}
.ColoringPage-color-picker {
    margin: auto; 
    width: 75%; 
    padding: 1rem; 
    border-radius: 5px; 
}

.ColoringPage-book-container {
    width: 90%; 
    margin: auto; 
    height: 100%; 
}
body {
    height: 100vh;
    background: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.5)),
    url(/static/media/photo-1516383607781-913a19294fd1.01a9d8f8.jpg) no-repeat center center fixed;
background-size: cover; 
}

 @media screen and (max-width: 769px) {
     .ColoringPage-sketch{
         padding-left: 2.5rem; 
     }
     .ColoringPage-color-picker {
         width: 100vw; 
     }
 }

 
.SVG:hover {
    cursor: pointer;
    
}

.SVG-left , .SVG-right {
    background-color: rgb(243, 239, 239);
    height: 100%; 
    
    
    
}

.SVG-text {
    font-family:"Playfair Display", serif; 
    line-height: 1.5rem;
}


.flipbook {
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.5);
    overflow: hidden; 
}

.SVG-left {
    border-right: 0;
    box-shadow: inset -7px 0 30px -7px rgba(0, 0, 0, 0.4);
}

.SVG-right {
    border-left: 0;
    box-shadow: inset 7px 0 30px -7px rgba(0, 0, 0, 0.4);
}

.SVG-save {
   margin-top: -2rem; 
   padding-bottom: 2rem; 
}

.MyPictures-svg-container {
    position: relative;
    margin: 1rem; 
    
}

.SVG-button {
    position: absolute; 
    bottom: 0; 
}
.SVG-mobile-view {
    display: none; 
    margin-top: 3rem;
}

@media screen and (max-width:769px) {
    .flipbook-container {
        width: 100%; 
        height: 100%; 
    }
    .flipbook {
        width: 100%; 
    }
    .SVG-mobile-view {
        display: block; 
    }
    .flipbook-container {
        display: none; 
    }
}


#carouselDevelopers img {
    width: 250px;
}

#title{
    width: 600px;
}

#express {
    width: 50px;
}

.LandingPage { 
    background: url(/static/media/BackgroundRainbow.05b059c1.jpg) no-repeat center center fixed;
    background-size: cover;
}

a {
    text-decoration: none !important;
}

li {
    list-style-type: none;
}
.MyPictures {
    display: flex; 
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 1rem; 
    padding-top: 5rem; 
}

.MyPictures-svg{
    pointer-events: none;
    width: 20rem; 
    margin: 1rem 1rem 1rem 0; 
    border: 1px solid grey; 
    padding: 0.5rem 0 0.5rem 0; 
    height: 100%; 
}








  
  
